	export default {
        onLoad(options) {
            uni.showLoading({
                title: '加载中...',
                mask: false
            });
            // console.log(options)
            this.id = options.id
          uni.request({
              url: 'https://www.apiopen.top/satinCommentApi', //仅为示例，并非真实接口地址。
              data: {
                  id: options.id,
                  page: this.page
              },
              header: {
                  // 'custom-header': 'hello' //自定义请求头信息
              },
              success: (res) => {
                  // console.log(res.data);
                  uni.hideLoading();
                  if(res.data.code===200){
                      this.hotList = res.data.data.hot.list
                      this.normalList = res.data.data.normal.list
                  }
                  
              }
          });  
        },
		data() {
			return {
                id:null,
				page:1,
                hotList:[],
                normalList:[]
			}
		},
		methods: {
			
		}
	}
